export default "@import '~coveo-styleguide/scss/common/palette.scss';\r\n@import '~coveo-styleguide/scss/variables.scss';\r\n\r\n.banner {\r\n    position: relative;\r\n    padding: $banner-padding;\r\n    background: linear-gradient(45deg, $mirage 0%, $science-blue 100%);\r\n    box-shadow: $banner-box-shadow;\r\n}\r\n\r\n.bannerName {\r\n    color: $pure-white;\r\n    font-size: $small-font-size;\r\n    text-transform: uppercase;\r\n    opacity: $transparency-3;\r\n}\r\n\r\n.bannerSubtitle {\r\n    color: $pure-white;\r\n    font-size: $big-font-size;\r\n}\r\n\r\n.bannerMain {\r\n    box-sizing: border-box;\r\n    width: 100%;\r\n    margin-top: 2 * $spacing;\r\n    margin-bottom: 2 * $spacing;\r\n    color: $pure-white;\r\n}\r\n\r\n.bannerTitle {\r\n    font-size: $big-title-font-size;\r\n}\r\n\r\n.bannerWarningTitle {\r\n    position: relative;\r\n    color: $school-bus-yellow;\r\n}\r\n\r\n.bannerErrorTitle {\r\n    color: $red;\r\n}\r\n\r\n.bannerWarningIcon {\r\n    position: relative;\r\n    top: $banner-warning-icon-fix;\r\n    fill: $school-bus-yellow;\r\n\r\n    .bannerErrorTitle & {\r\n        fill: $red;\r\n    }\r\n}\r\n\r\n.bannerDescription {\r\n    color: rgba($pure-white, $transparency-2);\r\n    font-size: $medium-title-font-size;\r\n}\r\n\r\n.bannerRight {\r\n    color: rgba($pure-white, $transparency-2);\r\n    font-size: $default-font-size;\r\n}\r\n\r\n.bannerBottomRightInfos {\r\n    display: inline;\r\n    max-width: $banner-right-width;\r\n}\r\n"